.ButtonContainer {
    position: relative;

    &:hover .ArrowUp{
        border-bottom: 5px solid white;
    }
}

.WidgetButton {
    height: 33px;
    width: 104px;
    padding: 0;
    border: none;
    border-radius: 0px;

    font-family: 'HK Grotesk','Source Sans Pro';
    text-decoration: none;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 13px;

    color: black;
    transition: ease-in-out 0.2s;

    &:hover, &.selected {
        background-color: #2a2b2b;
        cursor: pointer;
        color: white;
    }
}
.ArrowUp {
    position: absolute;
    margin: auto;
    margin-top: -5px;
    margin-left: 39px;
    z-index: 5;
    width: 0px; 
    height: 0px; 
    border-left: 11px solid transparent;
    border-right: 11px solid transparent;
    border-bottom: 5px solid transparent;
    transition: ease-in-out 0.2s;

    &.selected{
        border-bottom: 5px solid white;
    }
}